.list {
    --li-margin: 1rem;
}

.list-item {
    display: flex;
    justify-content: space-between;
    border: 1px solid black;
    margin-bottom: var(--li-margin);
    padding: 0.7rem;
    font-size: 1.2rem;
}
@media (min-width:700px) {
    .list {
        --li-margin: 0;
    }
}


